*{
  margin: 0px;
  padding: 0px;
}

.main{
  display: flex;
  height: 60vh;
  justify-content: center;
  align-items: center;
  background-color: #252525;
}

.result{
  background-color: white;
  height: 27vh;
  width: 17vw;
  border-radius: 25px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  font-weight: bold;
  color: #225C6E;
}

.navbar {
  background-color: whitesmoke;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 75px;
}


body{
  height: 90vh;
  background-color: #252525;
}

a{
  text-decoration: none;
  margin: 0px 25px;
  color: #252525;
}


label{
  font-weight: bold;
  color: #225C6E;
}

p{
  color: #225C6E;
}